.case-button {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  padding: 14px 46px;
  color: var(--dark-color);
  border: 1px solid var(--dark-color);
  transition: all 0.6s ease-in-out;
}
.case-button:hover {
  background-color: var(--dark-color);
  color: var(--light-color);
}